:root{
    --color-0:#000;
    --color-1:#B7094C;
    --color-2:#D4A373;
    --color-3:#E9ECEF;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
::selection{
    color: var(--color-0);
    background: var(--color-2);
}
html{
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
    scroll-snap-points-y: repeat(100%);
}
body{
    height: 100vh;
    width: 100%;
    background: var(--color-0);
    scroll-behavior: smooth;
    cursor: none;
}
body::-webkit-scrollbar{
    width:0px;
}
#cursor{
    position: absolute;
    height: 3vh;
    width: 3vh;
    border-radius: 50%;
    background: transparent;
    border: 1px solid var(--color-2);
    z-index: 100000000000000000000;
    background: #d4a37344;
}
body>div:not(.pg3,.wrapper,#cursor,.nav){
    scroll-snap-align: center;
    height: 100vh;
    width: 100%;
    display: flex;
    background: var(--color-3);
    align-items: center;
    justify-content: center;
    color: var(--color-2);
}
button{
    border: 1px solid var(--color-2);
    background: transparent;
    color: var(--color-2);
    font-weight: 100;
    width: 17vh;
    height: 4vh;
    transition: .3s;
}
button:active{
    filter: brightness(50%);
}
button:hover{
    background: var(--color-2);
    color: var(--color-0);
}
.nav{
    position: sticky;
    top: 10%;
    left:90%;
    width: fit-content;
}
.wrapper{
    background: transparent;
    position: absolute;
    display: flex;
    justify-content: center;
}
.i{
    left: 00%;
    height: 230vh;
    width: 8%;
}
.ii{
    height: 120vh;
    width: 15%;
    left: 85%;
}
.ii a{
    position: sticky;
    top: 3%;
    height: fit-content;
    font-family: 'Abril Fatface', cursive;
    font-size: 4vh;
    transition-duration: .3s;
}
a{
    color: var(--color-3);
    z-index: 100000000000000;
}
a:hover{
    text-decoration: none;
    cursor: none;
    color: var(--color-0);
}

.lines{
    position: sticky;
    top: 50%;
    transform: translate(0%,-50%);
    height:fit-content;
    width: 100%;
    display: grid;
    gap: 3vh;
}
.lines hr{
    position: relative;
    left: 10%;
    height: 2px;
    transition-duration: .3s;
}
