.up{
    width: 80%;
}
.down{
    width: 40%;
}
.black{
    background-color: var(--color-0);
    color: var(--color-0);
}
.white{
    background-color: var(--color-3);
    color: var(--color-3);
}
.ii a{
    background: transparent;
}
.pg1 h1{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: var(--color-3);
    font-family: 'El Messiri', sans-serif;
    font-size: 10vh;
    z-index: 4;
}
.bg{
    width: 100%;
    height: 100%;
    align-self: flex-start;
    text-align: center;
}
.bg img{
    height: 80%;
    width: 50%;
    padding: 0 1vh 1vh 1vh;
    border: 1px double var(--color-2);
    border-top: 0px;
    position: sticky;
    top: 0%;
}
#myContent{
    width: 90%;
    height: 80%;
    display: flex;
    align-items: center;
    flex-direction: row;
    color: var(--color-0);
}
#myContent div{
    height: contain;
    transition: 1.4s;
}
.inf{
    justify-self: stretch;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    font-family: 'Red Rose', cursive;
    height: 70%;
    gap: 30%;
    padding: 0 0 0 10%;
    transform: translateX(-30%);
    opacity: 0%;
    max-width: 60%;
}
[data-inviewport="inf"].is-inViewport{
    transform: translateX(0%);
    opacity: 100%;
}
.inf #ttl{
    font-size: 3vh;
    letter-spacing: 1vh;
    width: fit-content;
}
.inf #txt{
    letter-spacing: .3vh;
    font-size: 1.6vh;
    max-width: 60%;
    height: 100%;
}
[data-inviewport="pic"]{
    justify-self: end;
    height:50%;
    width: 40%;
    border: 2px solid var(--color-2);
    z-index: 100000000000;
    transform: translateX(-70%);
    opacity: 0%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(/imgs/bigwave_japan-new1.png);
    background-size: cover;
}
[data-inviewport="pic"].is-inViewport{
    transform: translateX(0%);
    opacity: 100%;
}
#pic .imgHW{
    width: 90%;
    height: 90%;
    transition-duration: .3s;
    pointer-events: all;
    overflow: hidden;
}
.imgHW:hover{
    transform: scale(1.5) translateX(-20%);
}
