@keyframes mgO {
    0%{
        top: 00%;
    }
    50%{
        top: -50%;
    }
    100%{
        top: -100%;
    }
}
.imgHW img{
    position: relative;
    height: 100%;
    width: 100%;
    top: 0%;
    // animation: mgO 10s ease-in-out infinite alternate;
}
.hr{
    position: absolute;
    top: 190%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: fit-content;
    display: flex;
    align-items: center;
}
.hr hr:nth-child(1){
    width: 15vw;
    height: 1px;
    background: rgba(0, 0,0,.3);
    z-index: 1;
}
.hr hr:nth-child(2){
    width: 15vw;
    height: 3px;
    position:absolute;
    top: 0%;
    left: 00%;
    border-radius: 5rem;
    background: var(--color-2);
    z-index: 2;
    transform: translate(0%,-25%);
    transition-duration:.5s ;
}
.hr hr:nth-child(2):after{
    content: '';
    height: 1.5vh;
    width: 1.5vh;
    border-radius: 50%;
    background-color: var(--color-2);
    position: absolute;
    transform: translate(-50%,-50%);
    transition-duration: .3s;
    left: 100%;
    z-index: 2;
}
.pg2>.fas{
    position:absolute;
    font-size: 4vh;
    transition-duration: .5s;
    top: 190%;
    transform: translate(-50%,-50%);
    /* animation: moves  .5s linear ease-in; */
    color: var(--color-2);
    opacity: 00%;
}
.pg2>.fa-chevron-left{
    left: 35%;
}
.pg2>.fa-chevron-right{
    left: 65%;
}
.imgHW>.fas{
    position: absolute;
    top: 00%;
    font-size: 3.5vh;
    z-index: 1000000000000;
    height: 100%;
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition-duration: .5s;
    color: transparent;
    cursor: default;
}
.imgHW>.fas:hover{
    background: rgba(255,255,255,.1);
    backdrop-filter: blur(4px);
    color: var(--color-2);
}
#prevImg{
    left: 0%;
}
#nextImg{
    left: 70%;
}

.pg3{
    scroll-snap-align: center;
    height: 30vh;
    width: 100%;
    display: flex;
    background: var(--color-3);
    align-items: center;
    justify-content: center;
    color: var(--color-2);
    background-image: url(../imgs/joakim-honkasalo-ssvjJLB6wIw.jpg);
    background-size: 50% 90%;
    background-color: #000;
    background-repeat:no-repeat;
    background-attachment: fixed;
    background-position: top;
}
.pg3 a{
    font-family: 'Abril Fatface', cursive;
    font-size: 4vh;
    transition-duration: .3s;
}
